var _self , logRes;
export default {
	data() {
		return {
			userid:'',
			msg:'',
			productCode:''
		}
	},
	onLoad() {
		_self = this;
		
		logRes = _self.checkLogin('../my/getIntegral',1);
		
		_self.userid = logRes[0];
		
		//console.info(_self.did);
	},
	methods: {
		//返回
		toReturn:function(){
			uni.navigateBack({
				delta: 1
			});
		},
		//获得积分
		getIntegral:function(){
			console.log(_self.did);
			console.log(_self.productCode);
			
			uni.request({
				url: _self.apiServer+'mall/code/getIntegral',
				method: 'PUT',
				data: {
					'productCode':_self.productCode,
					'userid':_self.userid
				},
				header: {'content-type' : "application/x-www-form-urlencoded"},
				success: res => {
					console.log(res.data);
					//当状态码为200 成功
					if(res.data.status == 200){					
						uni.showToast({
							title:"恭喜您,兑换成功",
							icon:"success",
							
						})
						_self.msg = "恭喜您，兑换成功！"
						
					}
					//判断是否为正品
					else if(res.data.status == 10012){
						uni.showToast({
							title:res.data.msg,
							icon:"none",
							
						})
						_self.msg = res.data.msg
						
					}
					//判断是否为窜货
					else if(res.data.status == 10013){
						uni.showToast({
							title:res.data.msg,
							icon:"none",
							
						})
						_self.msg = res.data.msg
						
					}
					//判断是否已经被领取了积分
					else if(res.data.status == 10014){
						uni.showToast({
							title:res.data.msg,
							icon:"none",
							
						})
						_self.msg = res.data.msg
						
					}
					//判断userid的用户是否存在
					else if(res.data.status == 10015){
						uni.showToast({
							title:res.data.msg,
							icon:"none",
							
						})
						_self.msg = res.data.msg
						
					}else{
						uni.showToast({
							title:res.data.msg,
							icon:"none",
							
						})
						_self.msg = res.data.msg
					}
					
				},
				fail: (e) => {
					console.log(e);
					_self.msg = "网络不流畅，请稍后再试"
				},
				complete: () => {}
			});
			
		}
	}
}
